.result {
  margin-block-start: calc(var(--clear-unit) * 2);
  margin-block-end: calc(var(--clear-unit) * 2);
  background: var(--clear-zebra);
  padding: calc(var(--clear-unit) * 4) calc(var(--clear-unit) * 3);
  text-align: center;
  font-size: 1.25rem;
  border-radius: calc(var(--clear-unit) * 2);
}

.critical {
  color: var(--clear-background);
  background: var(--clear-error);
}

.footer {
  text-align: right;
  font-size: var(--clear-font-size-label);
}
